.navigation {

height: 80px;
display: flex;
justify-content: space-between;
align-items: center;

position: absolute;
top: 0;
width: 100%;
left: 0;

& .logo {
margin: 15px;
height: 100%;
width: 15%;
font-size: 2rem;
text-shadow: 3px 3px 1px red, -3px -3px 1px cyan;
@include center;
padding: 10px;

@media screen and (max-width: 460px) {
    width: 50%;
    padding-right: 20px;
     }
}

.burger {
    cursor: pointer;
    display: none;
    width: 100%;
    height: 100%;
    position: absolute;

    @media screen and (max-width: 460px) {
        right: 30px;
         }

    & span {
        right: 10%;
        position: absolute;

        &:before {
            content: "";
            width: 40px;
            height: 3px;
            background: $color2;        
            position: absolute;
        border-radius: 30px;
            
        }
        &:nth-child(1){
            top: 30%;
        }
        &:nth-child(2){
            top: 42%;
        }
        &:nth-child(3){
            top: 54%;
        }
    }
@media screen and (max-width: 970px) {
    display: block;
    z-index: 55;
}
}



& ul {
display: flex;
justify-content: space-around;
align-items: center;
margin: 0;

@media screen and (max-width: 970px) {
    width: 100vw;
    height: 100vh;
    position: absolute;
    flex-direction: column;
    @include center;
    font-size: 2.2rem;
backdrop-filter: blur(5px);
background: rgba(0, 0, 0, 0.9);
top: 0;
z-index: 50;
padding: 0;
margin-left: -100%;
transition: all .3s ease-in-out ;
}




& li {
    margin-right: 20px;
    transition: 0.4s ease-in-out;

    @media screen and (max-width: 970px) {
        padding-bottom: 30px;
        text-transform: uppercase;
    }

    &:hover {

            text-shadow: 3px 3px 1px red, -3px -3px 1px cyan;
    }
}

& .navActive:before {
    content: "";
    position: absolute;
    width: 80%;
    height: 1px;
    background: linear-gradient(to left top, cyan, red);
    top: 150%;
    left: 0;
    animation: line 0.3s linear forwards;

    @media screen and (max-width: 970px) {
        display: none;
    }

}
@keyframes line {
    0% {
        width: 0%;
    }
    100% {
        width: 80%;
    }
}

}
}

ul.menu-burger {
    margin-left: 0;

    }