@import url("https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Poiret+One&display=swap");
body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  list-style: none;
  background: #0A0A0A;
  font-family: "Poiret One", cursive;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
body ::selection {
  color: #0A0A0A;
  background: rgb(243, 243, 243);
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: rgb(243, 243, 243);
  transition: 0.4s ease-in-out;
  position: relative;
}
a:hover {
  text-shadow: 3px 3px 1px red, -3px -3px 1px cyan;
}
a:visited {
  color: rgb(243, 243, 243);
}

h1 {
  display: none;
}

h3, p {
  font-family: "Major Mono Display", monospace;
  color: rgb(243, 243, 243);
  font-size: 4rem;
}

#zero {
  display: grid;
  grid-template-areas: "title pic" "txt txt";
  color: rgb(243, 243, 243);
  padding: 20px;
  margin: 0 5%;
  background: #0A0A0A;
  width: 100%;
  height: 100%;
}

.text-content {
  grid-area: title;
  height: 80vh;
}

h2 {
  font-family: "Major Mono Display", monospace;
  font-size: 10.5rem;
  padding-bottom: 50px;
}
@media screen and (max-width: 620px) {
  h2 {
    font-size: 7rem;
    padding-top: 25%;
  }
}
@media screen and (max-width: 480px) {
  h2 {
    font-size: 5rem;
    padding-top: 35%;
  }
}

.invisible-text {
  opacity: 0.05;
  margin: 0;
  transform: rotate(190deg) translate(30%, -50%);
}

.glitch {
  position: absolute;
  top: 0;
}

.glitch:nth-child(2) {
  color: #f03e3e;
  animation: skewAnim 10s infinite;
  animation: skewAnim 3s infinite;
}

.glitch:nth-child(3) {
  color: #3df0cf;
  animation: glitchAnim2 10s infinite;
  animation: glitchAnim2 3s infinite;
}

.glitch:nth-child(4) {
  color: #f1f1f1;
  animation: glitchAnim1 10s infinite;
  animation: glitchAnim1 3s infinite;
}

@keyframes glitchAnim1 {
  7% {
    transform: none;
  }
  10% {
    transform: translate(6px, -2px);
  }
  13% {
    transform: none;
  }
  20% {
    transform: none;
  }
  23% {
    transform: translate(-12px, -7px);
  }
  26% {
    transform: none;
  }
  40% {
    transform: none;
  }
  43% {
    transform: translate(10px, -9px);
  }
  46% {
    transform: none;
  }
  65% {
    transform: none;
  }
  68% {
    transform: translate(7px, 5px);
  }
  71% {
    transform: none;
  }
  100% {
    transform: none;
  }
}
@keyframes glitchAnim2 {
  7% {
    transform: none;
  }
  10% {
    transform: translate(-6px, 2px);
  }
  13% {
    transform: none;
  }
  20% {
    transform: none;
  }
  23% {
    transform: translate(12px, 7px);
  }
  26% {
    transform: none;
  }
  40% {
    transform: none;
  }
  43% {
    transform: translate(-10px, 9px);
  }
  46% {
    transform: none;
  }
  65% {
    transform: none;
  }
  68% {
    transform: translate(-7px, 5px);
  }
  71% {
    transform: none;
  }
  100% {
    transform: none;
  }
}
@keyframes skewAnim {
  20% {
    transform: none;
  }
  23% {
    transform: skew(5deg, -5deg) translate(10px, 5px);
  }
  26% {
    transform: none;
  }
  40% {
    transform: none;
  }
  43% {
    transform: skew(5deg, -10deg) translate(-5px, 2px);
  }
  46% {
    transform: none;
  }
  100% {
    transform: none;
  }
}
