}
.profil-pic :after, .profil-pic :before {
  position: relative;
  z-index: 2;
  border-radius: 100%;
}

.profil-pic:before {
  content: "";
  position: absolute;
  background: url(./Assets/imgs/prof.jpeg) no-repeat center;
  width: 350px;
  height: 415px;
  right: 1%;
  animation: bugImg 2s 0s linear reverse infinite;
