.modal {
  z-index: 4;
}

.modal, .overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  transition: 0.2s ease-in-out;
  animation: popup 0.4s linear;
}

@keyframes popup {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%;
  }
}
.overlay {
  background: rgba(49, 49, 49, 0.8);
  z-index: 3;
  position: relative;
}

.modal-content {
  position: absolute;
  display: grid;
  grid-template-areas: "h2 h2" "p img";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  line-height: 1.4;
  background: #0A0A0A;
  padding: 14px 28px;
  border-radius: 3px;
  height: 85vh;
  width: 85vw;
  max-width: 90vw;
  min-width: 300px;
  z-index: 4;
}
@media screen and (max-width: 620px) {
  .modal-content {
    grid-template-rows: 20% 80%;
  }
}
.modal-content h2 {
  color: rgb(243, 243, 243);
  font-size: 2.1rem;
  grid-area: h2;
}
@media screen and (max-width: 620px) {
  .modal-content h2 {
    padding: 0;
    font-size: 1.7rem;
  }
}
@media screen and (max-width: 430px) {
  .modal-content h2 {
    font-size: 1.2rem;
  }
}
.modal-content p {
  color: rgb(243, 243, 243);
  font-size: 1.1rem;
  font-family: "Poiret One", cursive;
  grid-area: p;
  padding: 20px;
  width: 80%;
  margin: 0;
}
@media screen and (max-width: 520px) {
