  border-radius: 30px;
}
@media screen and (max-width: 900px) {
  .modal-content img {
    display: none;
  }
}
.modal-content a {
  position: relative;
  width: 100%;
}
.modal-content a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: linear-gradient(to left top, cyan, red);
  top: 120%;
  left: 0;
}

