.navigation {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
}
.navigation .logo {
  margin: 15px;
  height: 100%;
  width: 15%;
