}
@media screen and (max-width: 460px) {
  .navigation .logo {
    width: 50%;
    padding-right: 20px;
  }
}
.navigation .burger {
  cursor: pointer;
  display: none;
  width: 100%;
  height: 100%;
  position: absolute;
}
@media screen and (max-width: 460px) {
  .navigation .burger {
    right: 30px;
  }
}
.navigation .burger span {
  right: 10%;
  position: absolute;
}
.navigation .burger span:before {
  content: "";
  width: 40px;
  height: 3px;
  background: rgb(243, 243, 243);
  position: absolute;
  border-radius: 30px;
}
.navigation .burger span:nth-child(1) {
  top: 30%;
}
.navigation .burger span:nth-child(2) {
  top: 42%;
}
.navigation .burger span:nth-child(3) {
