#zero {
display: grid;
grid-template-areas: "title pic"
"txt txt";
    color: $color2;
    padding: 20px;
    margin: 0 5%;
    background: $color1;
    width: 100%;
    height: 100%;
}

.text-content {
  grid-area: title;
  height: 80vh;

}

h2 {
    font-family: $font-title;
    font-size: 10.5rem;
    padding-bottom: 50px;

    @media screen and (max-width: 620px) {
      font-size: 7rem;
      padding-top: 25%;
    }
    @media screen and (max-width: 480px) {
      font-size: 5rem;
      padding-top: 35%;
}
}

.invisible-text {
opacity: 0.05;
margin: 0;
transform: rotate(190deg) translate(30%, -50%);
}

.glitch {
    position: absolute;
    top: 0;
}

.glitch:nth-child(2) {
    color: #f03e3e;
    animation: skewAnim 10s infinite;
    animation: skewAnim 3s infinite;
  }
  .glitch:nth-child(3) {
    color: #3df0cf;
    animation: glitchAnim2 10s infinite;
    animation: glitchAnim2 3s infinite;
  }
  .glitch:nth-child(4) {
    color: #f1f1f1;
    animation: glitchAnim1 10s infinite;
    animation: glitchAnim1 3s infinite;
  }

  @keyframes glitchAnim1 {
    7% {
      transform: none;
    }
    10% {
      transform: translate(6px, -2px);
    }
    13% {
      transform: none;
    }
    20% {
      transform: none;
    }
    23% {
      transform: translate(-12px, -7px);
    }
    26% {
      transform: none;
    }
    40% {
      transform: none;
    }
    43% {
      transform: translate(10px, -9px);
    }
    46% {
      transform: none;
    }
    65% {
      transform: none;
    }
    68% {
      transform: translate(7px, 5px);
    }
    71% {
      transform: none;
    }
    100% {
      transform: none;
    }
  }
  @keyframes glitchAnim2 {
    7% {
      transform: none;
    }
    10% {
      transform: translate(-6px, 2px);
    }
    13% {
      transform: none;
    }
    20% {
      transform: none;
    }
    23% {
      transform: translate(12px, 7px);
    }
    26% {
      transform: none;
    }
    40% {
      transform: none;
    }
    43% {
      transform: translate(-10px, 9px);
    }
    46% {
      transform: none;
    }
    65% {
      transform: none;
    }
    68% {
      transform: translate(-7px, 5px);
    }
    71% {
      transform: none;
    }
    100% {
      transform: none;
    }
  }
  @keyframes skewAnim {
    20% {
      transform: none;
    }
    23% {
      transform: skew(5deg, -5deg) translate(10px, 5px);
    }
    26% {
      transform: none;
    }
    40% {
      transform: none;
    }
    43% {
      transform: skew(5deg, -10deg) translate(-5px, 2px);
    }
    46% {
      transform: none;
    }
    100% {
      transform: none;
    }
  }

  .title {
    font-size: 4rem;
    font-family: $font-title;
    display: grid;
    grid-template-areas: "dev dev"
    "fe point";
    margin-top: 10%;

    @media screen and (max-width: 480px) {
      font-size: 2rem;
}


    & h3 {
        margin: 0;
        @media screen and (max-width: 620px) {
          font-size: 3rem;
         
  }
  @media screen and (max-width: 480px) {
    font-size: 2rem;
}
    }

    & #dev {
        grid-area: dev;
    }

    & #fe {
        grid-area: fe;
    }

    & #point {
        grid-area: point;
        position: relative;

        & .glitch {
left: 0;
        }
    }
  }

.pc {
  grid-area: pic;
   height: 80vh;
   display: flex;
   justify-content: center;
   align-items: center;
}

.pc img {
  padding-top: 30%;
width: 100%;
    @media screen and (max-width: 950px) {
      width: 0;
}
}

.transition {
grid-area: txt;
@include center;

& p {
  font-family: $font-common;
  font-size: 0.7rem;
  letter-spacing: 1rem;
  @media screen and (max-width: 810px) {
    font-size: .5rem;
    letter-spacing: 0.7rem;
}
@media screen and (max-width: 585px) {
  font-size: .9rem;
  letter-spacing: 0rem;
}
}
}
