#third {
   
    @include center;
    
    & .content {
        flex-direction: column;
    width: 70vw;
    height: 70vh;
    background: #f3f3f315;
    @include center;
    margin-top: 15%;
    border-radius: 30px;
    color: red;
    
    & h3 {
        color: $color2;
        margin: 0 auto;
        font-size: 3rem;
        @media screen and (max-width: 990px) {
            font-size: 2rem;
 }
 @media screen and (max-width: 610px) {
font-size: 1.5rem;;
 }
 @media screen and (max-width: 460px) {
font-size: 1.2rem;
      margin-bottom: 20px;                                                  
}
@media screen and (max-width: 370px) {
   font-size: 1rem;
                }
    }

    & p {
        font-family: $font-common;
        font-size: 1.3rem;
        padding: 15px;
        text-align: center;
        @media screen and (max-width: 990px) {
            font-size: 1.1rem;
            margin: 5%;
                        }
                        @media screen and (max-width: 610px) {
margin: 0;
}
@media screen and (max-width: 460px) {
padding: 0;
font-size: 1rem;
}
@media screen and (max-width: 370px) {
font-size: 0.9rem;
                }
    }

    & a:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        background: linear-gradient(to left top, cyan, red);
        top: 120%;
        left: 0;
      }
      }
}
