body.active-modal {
  overflow-y: hidden;
}

#first {
    color: $color2;
    width: 95vw;
    height: 80vh;
    @include center;
    margin: 0 auto;
    border-radius: 15px;
    }


.container {
  width: 100%;
  height: 100%;
  @include center;

  & a {
    width: 100%;
    height: 100%;
  }

}


.profil-pic {
border-radius: 100%;
position: relative;
background: url(./Assets/imgs/prof.jpeg) no-repeat center;
width: 350px;
height: 415px;
cursor: pointer;
transition: all 0.3s ease-out;
margin: 10% auto;

@media screen and (max-width: 660px) {
  width: 70%;
  height: 90%;

}
@media screen and (max-width: 360px) {

border-radius: 0;
}

 & :after, :before {
  position: relative;
  z-index: 2;
  border-radius: 100% ;
 }
}

.profil-pic:before {
        content: "";
position: absolute;
background: url(./Assets/imgs/prof.jpeg) no-repeat center;
width: 350px;
height: 415px;
right: 1%;
animation: bugImg 2s 0s linear reverse infinite;
cursor: pointer;
@media screen and (max-width: 660px) {
  width: 100%;
  height: 100%;
   }
    }

    .profil-pic:after {
      content: "";
      position: absolute;
      background: url(./Assets/imgs/prof.jpeg) no-repeat center;
      width: 350px;
      height: 415px;
      left: 1%;
      animation: bugImg2 2s 0s linear reverse infinite;
      cursor: pointer;
      @media screen and (max-width: 660px) {
        width: 100%;
        height: 100%;
         }
    }
    
    @keyframes bugImg {
      0% {
        clip-path: inset(132px 120px 101px 300px);
      }
    5% {
      clip-path: inset(170px 120px 94px 300px);
    }
    10% {
      clip-path: inset(400px 120px 66px 300px);
    }
    15% {
      clip-path: inset(87px 120px 82px 30px);
    }
    20% {
      clip-path: inset(137px 120px 61px 30px);
    }
    25% {
      clip-path: inset(340px 120px 140px 30px);
    }
    30% {
      clip-path: inset(133px 12px 74px 30px);
    }
    35% {
      clip-path: inset(76px 120px 107px 300px);
    }
    40% {
      clip-path: inset(59px 12px 130px 300px);
    }
    45% {
      clip-path: inset(290px 120px 84px 30px);
    }
    50% {
      clip-path: inset(220px 12px 67px 300px);
    }
    55% {
      clip-path: inset(67px 120px 62px 30px);
    }
    60% {
      clip-path: inset(100px 12px 105px 300px);
    }
    65% {
      clip-path: inset(78px 120px 115px 300px);
    }
    70% {
      clip-path: inset(105px 12px 130px 30px);
    }
    75% {
      clip-path: inset(150px 12px 75px 300px);
    }
    80% {
      clip-path: inset(66px 120px 390px 30px);
    }
    85% {
      clip-path: inset(133px 120px 73px 300px);
    }
    90% {
      clip-path: inset(360px 12px 128px 30px);
    }
    95% {
      clip-path: inset(68px 102px 103px 30px);
    }
    100% {
      clip-path: inset(140px 12px 100px 300px);
    }
  }

