#second {
    width: 90vw;
    height: 90vh;

    & .crea {
        height: 100%;
    }
       

    & .cardz {
        width: 100%;
        height: 100%;
        @include center;

        & ul {
            display: grid;
            grid-template-columns: 33% 33% 33% ;

            @media screen and (max-width: 1000px) {
grid-template-columns: 50% 50%;
                            }
                            @media screen and (max-width: 555px) {
   grid-template-columns: 100%;
                                            }
                


        & li {
            background: #f3f3f315;
                color: $color2;
            width: 300px;
            height: 200px;
            margin: 10px;
            @include center;
            transition: 0.4s ease-in-out;
            border-radius: 30px;
            cursor: pointer;

            @media screen and (max-width: 710px) {
                width: 225px;
                height: 125px;
                            }
                            @media screen and (max-width: 320px) {
width: 175px;
height: 75px;
                                            }
                


            &:hover {
                background: #f3f3f35d;
            color: $color1;
                
                font-size: 2rem;
            }
        }
    }
}
}

  
.modal {
  z-index: 4;
}
  .modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    transition: 0.2s ease-in-out;
    animation: popup 0.4s linear;
}

@keyframes popup {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 100%
  }
}

.overlay {
    background: rgba(49,49,49,0.8);
    z-index: 3;
    position: relative;

}
.modal-content {
    position: absolute;
    display: grid;
    grid-template-areas: "h2 h2"
    "p img";
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 1.4;
    background: $color1;
    padding: 14px 28px;
    border-radius: 3px;
    height: 85vh;
    width: 85vw;
    max-width: 90vw;
    min-width: 300px;
    z-index: 4;
    @media screen and (max-width: 620px) {
grid-template-rows: 20% 80%;
    }


    & h2 {
      color: $color2;
      font-size: 2.1rem;
      grid-area: h2;
       @media screen and (max-width: 620px) {
padding: 0;
font-size: 1.7rem;
}
       @media screen and (max-width: 430px) {
font-size: 1.2rem;
      }
    }
    & p {
      color: $color2;
      font-size: 1.1rem;
      font-family: $font-common;
      grid-area: p;
      padding: 20px;
      width: 80%;
      margin: 0;
      @media screen and (max-width: 520px) {
      padding: 0;
      font-size: 1rem;
      }
      @media screen and (max-width: 430px) {
        font-size: .9rem;
              }
    }

    & img {
      grid-area: img;
      height: 80%;
      width: 80%;
      padding: 20px;
      border-radius: 30px;
      @media screen and (max-width: 900px) {
       display: none;
      }
    }
& a {
position: relative;
width: 100%;
}

    & a::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background: linear-gradient(to left top, cyan, red);
      top: 120%;
      left: 0;
    }
  
}

#close-modal {
    position: absolute;
    top: 10px;
    right: 10px;
    padding: 5px 7px;
    z-index: 4;
    color: $color2;
    font-size: 2rem;
    border: none;
    cursor: pointer;
    background: transparent;
    box-shadow: none;
}
