@import url('https://fonts.googleapis.com/css2?family=Major+Mono+Display&family=Poiret+One&display=swap');



$font-title: 'Major Mono Display', monospace;
$font-common: 'Poiret One', cursive;

$color1: #0A0A0A ;
$color2: rgb(243, 243, 243);

@mixin center {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    box-sizing: border-box;
    list-style: none;
    background: $color1;
    font-family: $font-common;
    padding: 0;
    margin: 0;
    @include center;

    & ::selection {
      color: $color1;
      background: $color2;
    }
  }

  li { list-style: none;
}

  a {
    text-decoration: none;
    color: $color2;
    transition : .4s ease-in-out;
    position: relative;

    &:hover {
      text-shadow: 3px 3px 1px red, -3px -3px 1px cyan;
    }

    &:visited {
        color: $color2;
    }
  }

  h1 {
    display: none;
  }
  h3, p {
  font-family: $font-title;
  color: $color2;
  font-size: 4rem;
  }