.content {
    width: 95%;
    height: 70%;
@include center;
flex-direction: column;
    color: $color2;
    padding: 30px;
    margin-bottom: 10%;
    position: relative;

    & h2 {
        text-align: center;
        font-size: 2.5rem;
        margin: 0;    
        @media screen and (max-width: 940px) {
font-size: 2rem;
padding: 20px;
            }
            @media screen and (max-width: 650px) {
                font-size: 1.5rem;
                padding: 10px;
 }
             @media screen and (max-width: 350px) {
            font-size: 1rem;         
        margin-top: 50px;                                   }
        }
    & p {
        font-size: 1.2rem;
        padding: 10px;
        margin: 0 15%;

        @media screen and (max-width: 650px) {
            font-size: 1rem;
            margin: 0 5%;
                        }
                        @media screen and (max-width: 350px) {
                            margin: 0 10%;
                                        }
    }

    & a:before {
        
            content: "";
            position: absolute;
            width: 100%;
            height: 1px;
            background: linear-gradient(to left top, cyan, red);
            top: 125%;
            left: 0;
            animation: line 0.3s linear forwards;
        
            @media screen and (max-width: 970px) {
                display: none;
            }
        
        }
        @keyframes line {
            0% {
                width: 0%;
            }
            100% {
                width: 100%;
            }
        }
    }




.aboutme {
    margin-top: 5%;
position: absolute;
background: url(./Assets/imgs/prof7.jpeg) no-repeat top/cover;
bottom: 0;
left: 0;
width: 100%;
height: 175px;

@media screen and (max-width: 940px) {
height: 100px;
                }

                @media screen and (max-width: 350px) {
display: none;
                                }

  }