.contact {

    @include center;
    flex-direction: column;
    margin-top: 75px;
    
    & h3 {
        @include center;
        font-size: 2.5rem;
        @media screen and (max-width: 820px) {
            font-size: 1.5rem;
                        }
                        @media screen and (max-width: 470px) {
font-size: 1.2rem;
}
 @media screen and (max-width: 370px) {
font-size: 0.9rem;
 }
                            
            

    }

    input {
        border: 1px solid $color2;
        outline: none;
        background: #f3f3f315;
        color: $color2;
padding: 20px;
margin-left: 30px;
margin: 10px;
border-radius: 10px;
width: 300px;
font-family: $font-common;
font-size: 1rem;
transition: 0.2s ease-in-out;

@media screen and (max-width: 820px) {
    width: 150px;
                }
                @media screen and (max-width: 470px) {

                    height: 10px;
                                }

&:focus {
    border: red;
    box-shadow:   
    inset 0 0 1px 1px cyan, 1px 1px 1px 1px  red; 
}

    }
}
    
    
    .contact label {
        color: $color2;
        margin-left: 40px;
    }

    form {
      display: grid;
      grid-template-areas: "name msg"
      "mail msg"
      "btn btn";
      justify-content: left;

      @media screen and (max-width: 470px) {
        display: flex;
        @include center;
        flex-direction: column;
                    }
      
      & .form1 {
         display: flex;
          flex-direction: column;

      }

      & button {
        background: transparent;
        grid-area: btn;
        padding: 20px;
        margin: 15px auto;
        margin-top: 30px;
        width: 250px;
        border-radius: 10px;
        cursor: pointer;
        color: $color2;
        border: 1px solid $color2;
        transition: 0.3s ease-in-out;
        font-family: $font-common;
        font-size: 1.1rem;
        text-transform: uppercase;
        box-shadow: inset 5px 5px  red, -5px -5px cyan;
        border: none;

        @media screen and (max-width: 470px) {
            height: 50px;
            width: 100px;
                        }

    
        &:hover {
            transform : scale(1.05);
            border: none;
        }
    }

    & .formMessage p {
        font-size: 0.8rem;
        @include center;
        transition: ease-in-out .3s;
            }
      }
       #name {
grid-area: name;

      }
       #mail {
    grid-area: mail;
      }
       #msg {
    grid-area: msg;

    & input {
    width: 400px;
    height: 125px;
    border-radius: 15px;

    @media screen and (max-width: 820px) {
        width: 200px;
                    }
                    @media screen and (max-width: 470px) {

                        height: 30px;
                                    }
}


      }


    .reseaux {
    
    display: grid;
    grid-template-columns: 80% 20%;
    width: 100%;

    & ul {
        @include center;
        margin-left: 20%;

        @media screen and (max-width: 725px) {
            margin-left: 0;
            padding: 0;
                        }

        & li {
    margin: 30px;

    @media screen and (max-width: 470px) {

        margin: 0 auto;
                                                    }

        }
    }

        & i {
           font-size: 3.8rem; 
            transition: 0.4s ease-in-out;
            @media screen and (max-width: 725px) {
                font-size: 3rem;
                            }
                            @media screen and (max-width: 470px) {
font-size: 2rem;
                                            }

           &:hover {
            transform: scale(1.2);
            text-shadow: 5px 5px  red, -5px -5px cyan;
           }
        }
        & .copyright {
            position: relative;
            @media screen and (max-width: 725px) {
                right: 100%;
                            }
                            @media screen and (max-width: 470px) {
                                top: 0;
                                            }
                

        & p {
            font-family: $font-common;
            font-size: 1rem;
            margin: 20px;
            position: absolute;
            top: 40%;
            left: 30%;
            width: 100%;

            @media screen and (max-width: 470px) {
margin: 0;
top: 20%;
left: 100%;
                            }


        }
    }
    }

   