.notFound {
    @include center;
    flex-direction: column;
}

.anim1 {
    position: relative;
    & h2 {
        color: $color2;
        font-size: 8rem;
        margin: 30px;
        
    }
}
    

.invisible-text1 {
    position: relative;
    opacity: 0.05;
    margin: 0;
    transform: rotate(190deg) translate(30%, -50%);
    }
    
    .glitch1 {
        position: absolute;
        top: 0;
        
    }
    
    .glitch1:nth-child(2) {
        color: #f03e3e;
        animation: skewAnim 10s infinite;
        animation: skewAnim 3s infinite;
      }
      .glitch1:nth-child(3) {
        color: #3df0cf;
        animation: glitchAnim2 10s infinite;
        animation: glitchAnim2 3s infinite;
      }
      .glitch1:nth-child(4) {
        color: #f1f1f1;
        animation: glitchAnim1 10s infinite;
        animation: glitchAnim1 3s infinite;
      }
    
      @keyframes glitchAnim1 {
        7% {
          transform: none;
        }
        10% {
          transform: translate(6px, -2px);
        }
        13% {
          transform: none;
        }
        20% {
          transform: none;
        }
        23% {
          transform: translate(-12px, -7px);
        }
        26% {
          transform: none;
        }
        40% {
          transform: none;
        }
        43% {
          transform: translate(10px, -9px);
        }
        46% {
          transform: none;
        }
        65% {
          transform: none;
        }
        68% {
          transform: translate(7px, 5px);
        }
        71% {
          transform: none;
        }
        100% {
          transform: none;
        }
      }
      @keyframes glitchAnim2 {
        7% {
          transform: none;
        }
        10% {
          transform: translate(-6px, 2px);
        }
        13% {
          transform: none;
        }
        20% {
          transform: none;
        }
        23% {
          transform: translate(12px, 7px);
        }
        26% {
          transform: none;
        }
        40% {
          transform: none;
        }
        43% {
          transform: translate(-10px, 9px);
        }
        46% {
          transform: none;
        }
        65% {
          transform: none;
        }
        68% {
          transform: translate(-7px, 5px);
        }
        71% {
          transform: none;
        }
        100% {
          transform: none;
        }
      }
      @keyframes skewAnim {
        20% {
          transform: none;
        }
        23% {
          transform: skew(5deg, -5deg) translate(10px, 5px);
        }
        26% {
          transform: none;
        }
        40% {
          transform: none;
        }
        43% {
          transform: skew(5deg, -10deg) translate(-5px, 2px);
        }
        46% {
          transform: none;
        }
        100% {
          transform: none;
        }
      }

p {
    font-family: $font-common;
    text-align: center;
    margin: 30px;
    font-size: 2rem;
}

button {
    background: transparent;
    grid-area: btn;
    padding: 20px;
    margin: 15px auto;
    margin-top: 30px;
    width: 250px;
    border-radius: 10px;
    cursor: pointer;
    color: $color2;
    border: 1px solid $color2;
    transition: 0.3s ease-in-out;
    font-family: $font-common;
font-size: 1.1rem;
    text-transform: uppercase;
    box-shadow: inset 5px 5px  red, -5px -5px cyan;
    border: none;


    &:hover {
        transform : scale(1.05);
        border: none;
    }
}